import { mapState, mapActions, mapMutations } from 'vuex';
import AppLoading from '@/components/common/app-loading';

const PAGE_SIZE = 10;

export default {
  components: {
    AppLoading
  },
  data() {
    return {
      columns: [
        { label: '缩略图', width: 180, field: 'thumbnail', slot: 'thumbnail' },
        { label: '标题', width: 180, field: 'title', slot: 'title' },
        { label: '作者', width: 100, field: 'createUser', slot: 'author' },
        { label: '简介', field: 'abstract' },
        { label: '创建时间', width: 180, field: 'createTime' },
        { label: '更新时间', width: 180, field: 'updateTime' },
        { label: '公开', width: 80, field: 'status', slot: 'visible' },
        { label: '操作', width: 200, slot: 'operate' }
      ]
    };
  },
  computed: {
    ...mapState({
      userArticles: (state) => state.user.userArticles
    })
  },
  asyncData({ store, router }) {
    return store.dispatch('user/getUserArticles', {
      currentPage: 1,
      pageSize: PAGE_SIZE
    });
  },
  methods: {
    ...mapMutations({
      setUserArticle: 'user/setUserArticles'
    }),
    ...mapActions({
      getUserArticles: 'user/getUserArticles'
    }),
    togglePublish(status, id) {
      const { response } = this.$http({
        url: `/article/${id}`,
        method: 'put',
        data: {
          status
        },
        showSuccessMsg: '操作成功',
        showErrorMsg: true
      });

      response.then(
        () => {
          this.getUserArticles(id);
        },
        () => {}
      );
    },
    edit(article) {
      this.$router.push(`/admin/post/${article._id}`);
    },
    del(article) {
      this.$alert({
        title: '警告',
        text: '您确定要删除这篇文章吗？',
        icon: 'warning',
        buttons: true,
        dangerMode: true
      }).then((willDelete) => {
        if (willDelete) {
          this.deleteArticle(article._id);
        } else {
        }
      });
    },
    config(article) {
      this.$router.push(`/admin/article/config/${article._id}`);
    },
    deleteArticle(id) {
      const { response } = this.$http({
        url: `/article/${id}`,
        method: 'delete',
        showSuccessMsg: true,
        showErrorMsg: true
      });

      response.then(
        () => {
          this.getUserArticles({
            currentPage: 1,
            pageSize: PAGE_SIZE
          });
        },
        () => {}
      );
    }
  }
};
